@import url(https://fonts.googleapis.com/css?family=Open+Sans:800|Roboto:300&display=swap);
.App {
  padding: 1rem;
  margin: 2% 8%;
  height: 65vh; }

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

label {
  -webkit-user-select: none;
  /* Chrome/Safari */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* IE10+ */
  /* Rules below not implemented in browsers yet */
  -o-user-select: none;
  user-select: none; }

h1, h2, h3, h4, h5, h6 {
  font-family: "Open Sans", sans-serif; }

p, a, li {
  font-family: "Roboto", sans-serif; }

a {
  text-decoration: none; }

.builder {
  height: 100%; }
  .builder details > summary {
    cursor: pointer;
    padding: 1rem 0; }

.builder__categories {
  display: grid;
  grid-template-columns: repeat(4, 1fr) auto;
  grid-gap: 20px;
  height: 100%; }
  .builder__categories a {
    align-self: center; }

.foodCard {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  position: relative;
  color: white;
  padding: 0 2rem 5rem 2rem;
  background-color: lightblue; }
  .foodCard__container {
    max-height: 85%;
    overflow-y: auto;
    overflow-x: hidden; }
  .foodCard--Protein {
    background-color: #F16A70; }
  .foodCard--Grains {
    background-color: #8CDCDA; }
  .foodCard--Veggies {
    background-color: #B1D877; }
  .foodCard--Custom {
    background-color: #4D4D4D; }
  .foodCard > h3 > i {
    padding-left: 1rem; }
  .foodCard form {
    display: grid; }
    .foodCard form input[type="checkbox"] {
      display: none; }
    .foodCard form input[type="text"] {
      border-radius: 15px;
      border: 1px solid white;
      margin: 0.25rem;
      padding: 0.5rem 1rem;
      color: #F16A70;
      font-size: 1rem; }
    .foodCard form button[name="cancelAdd"], .foodCard form button[name="Add"] {
      justify-self: center;
      position: absolute;
      bottom: 1rem; }
    .foodCard form label {
      border-radius: 15px;
      border: 1px solid white;
      margin: 0.25rem;
      padding: 0.33333rem 1rem;
      -webkit-transition: 0.1s;
      transition: 0.1s;
      display: grid;
      grid-template-columns: 1fr auto;
      cursor: pointer; }
      .foodCard form label span:first-child {
        overflow-x: hidden; }
      .foodCard form label span:last-child {
        margin-left: 1rem; }
        .foodCard form label span:last-child:hover {
          -webkit-transform: scale(1.3);
                  transform: scale(1.3); }
      .foodCard form label:hover {
        background-color: white;
        color: #F16A70; }
  .foodCard form[name="foodCardBottom"] {
    display: flex;
    justify-content: center; }
    .foodCard form[name="foodCardBottom"] input {
      width: 100%; }

label.checked {
  background-color: white;
  color: #F16A70; }
  label.checked span i.fa-times {
    display: none; }

@media screen and (max-width: 1080px) {
  .builder__categories {
    grid-template-columns: 1fr;
    grid-template-rows: auto; }
    .builder__categories a {
      justify-self: center;
      padding-top: 2rem;
      padding-bottom: 2rem; }
  .foodCard {
    margin-top: 1rem;
    padding: 1rem 2rem 8rem 2rem; }
  button[name="cancelAdd"], button[name="Add"] {
    bottom: 2rem; } }

.resultsTitle {
  display: grid;
  grid-template-columns: auto 1fr;
  align-items: center;
  grid-gap: 20px; }

.resultsContainer {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fill, minmax(150px, 300px)); }

.resultCard {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  display: grid;
  grid-template-rows: auto 1fr;
  height: 100%;
  -webkit-transition: 0.1s;
  transition: 0.1s; }
  .resultCard:hover {
    -webkit-transform: scale(1.02);
            transform: scale(1.02); }
  .resultCard img {
    width: 100%;
    border-radius: 5px 5px 0 0; }
  .resultCard button {
    position: absolute;
    top: -35px; }
  .resultCard__title {
    color: white;
    position: relative;
    background-color: #F16A70;
    border-radius: 0 0 5px 5px;
    padding: 1.5rem; }
    .resultCard__title h3 {
      margin: 0; }

.recipe__summary {
  margin-top: 3rem;
  display: grid;
  grid-gap: 20px;
  grid-template-columns: 1fr 3fr; }

.recipe__ingredients {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  padding: 1rem 2rem;
  color: white;
  background-color: #F16A70; }

.recipe__buttons {
  display: grid;
  grid-template-columns: auto 1fr;
  grid-gap: 20px;
  margin-bottom: 2rem; }

.recipe img {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  width: 100%;
  height: 100%;
  object-fit: cover; }

@media screen and (max-width: 800px) {
  .recipe__summary {
    grid-template-columns: none;
    grid-template-rows: 1fr 1fr; } }

button {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.25);
  border-radius: 100%;
  color: #F16A70;
  border: 0;
  height: 3rem;
  width: 3rem;
  background-color: white;
  cursor: pointer; }
  button:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1); }
  button i {
    font-size: 1.3rem; }

.button--inverse {
  background-color: #F16A70;
  color: white; }

